import React, { Component } from "react";
import {
  Card,
  CardText,
  CardBody,
  CardTitle,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter
} from "reactstrap";
import { FaRegTrashAlt, FaRegEdit } from "react-icons/fa";
import { DelPost, EditPost } from "../../../redux/actions/PostCommentAction";
import { connect } from "react-redux";

class ListItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  onHandleClickToEdit = () => {
    const { id } = this.props.list_massage;
    const { EditPost } = this.props;
    EditPost(id);
  };
  onHandleClickToDel = () => {
    const { id } = this.props.list_massage;
    const { DelPost } = this.props;
    DelPost(id);
  };
  render() {
    const { id, data_field, id_users } = this.props.list_massage;
    return (
      <div>
        <Card className="blockpost">
          <div style={{ textAlign: "right" }}>
            <a name={id} onClick={() => this.onHandleClickToEdit()}>
              <FaRegEdit />
            </a>
            <a name={id} onClick={() => this.onHandleClickToDel()}>
              <FaRegTrashAlt />
            </a>
          </div>
          <CardBody>
            <CardTitle>{id_users}</CardTitle>
            <CardText>
              <span>{data_field}</span>
              <span>{}</span>
            </CardText>
          </CardBody>
        </Card>
        <div>
          <Modal
            isOpen={this.state.modal}
            toggle={this.toggle}
            className={this.props.className}
          >
            <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
            <ModalBody>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in
              reprehenderit in voluptate velit esse cillum dolore eu fugiat
              nulla pariatur. Excepteur sint occaecat cupidatat non proident,
              sunt in culpa qui officia deserunt mollit anim id est laborum.
            </ModalBody>
            <ModalFooter>
              <Button color="primary" onClick={this.toggle}>
                Do Something
              </Button>{" "}
              <Button color="secondary" onClick={this.toggle}>
                Cancel
              </Button>
            </ModalFooter>
          </Modal>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {};
};

export default connect(
  mapStateToProps,
  { DelPost, EditPost }
)(ListItem);
